<template>
	<div class="userdeta">
		<div>
			<div class="user-img">
				<img :src="userinfo.user_img" alt="" v-if="userinfo.user_img">
				<img src="@/assets/2.jpg" alt="" v-else>
			</div>
			<div class="user-edit">
				<div>
					<p>
						<span>0</span>
						<span class="user-text">粉丝</span>
					</p>
					<p>
						<span>0</span>
						<span class="user-text">关注</span>
					</p>
					<p>
						<span>0</span>
						<span class="user-text">获赞</span>
					</p>
				</div>

				<div @click="$router.push('/edit')">
					<div class="use-btn">编辑资料</div>
				</div>
			</div>
		</div>
		<div class="user">
			<h2>{{userinfo.name}}</h2>
			<p v-if="userinfo.user_desc">{{userinfo.user_desc}}</p>
			<p v-else>这个人优点懒</p>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['userinfo']
		
	}
</script>

<style scoped>
	.userdeta {
		background-color: #ffffff;
		padding: 0 12px;
	}

	.userdeta>div:nth-child(1) {
		display: flex;
	}

	.user h2 {
		margin: 0;
		padding: 0;
		font-weight: 400;
	}

	.user p {
		display: block;
		margin: 10px 0;
		padding: 0;
		color: #aaa;
	}

	.user-img img {
		margin-right: 20px;
		width: 85px;
		height: 85px;
		border-radius: 50%;
	}

	.user-edit {
		flex: 1;
	}

	.user-edit div:nth-child(1) {
		display: flex;
	}

	.user-edit div:nth-child(1) p {
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		font-size: 14px;
	}

	.user-edit div:nth-child(1) p:nth-child(1),
	p:nth-child(2) {
		border-right: 1px solid #aaa;
	}

	.user-text {
		color: #aaa;
	}

	.use-btn {
		padding: 5px 10px;
		margin-top: 10px;
		border: 1px solid #fb7a9f;
		padding: 3px;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fb7a9f;
		border-radius: 3px;
	}
</style>



