<template>
	<div class="btn" @click="onclick">
		<div class="loginbtn">{{pasbtn}}</div>
	</div>
</template>

<script>
	export default{
		props:['pasbtn'],
		methods:{
			onclick(){
				this.$emit('regsumid')
			}
		}
	}
</script>

<style>
	.btn{
		padding: 15px 10px;
	}
	.loginbtn{
		height: 45px;
		background-color: #ff9db5;
		color: #ffffff;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 16px;
		border-radius: 22.5px;
	}
</style>



