<template>
	<div @click="ban">
		<div class="editbanner">
			<span>{{left}}</span>
			<span>
				<slot name="right"></slot>
			</span>
		</div>
	</div>
</template>

<script>
	export default{
		props:['left'],
		methods:{
			ban(){
				this.$emit('asd')
			}
		}
	}
</script>

<style>
	.editbanner{
		background-color: #ffffff;
		padding: 15px;
		font-size: 16px;
		color: #333;
		border-bottom: 1px solid #eee;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
</style>



