<template>
	<div>
		<logintop midee='注册bilibili'>
			<!-- 使用编程式路由和插槽来跳转和占位的作用 -->
			<div slot='right' style="font-size: 13px;" @click="$router.push('/login')">登录</div>
		</logintop>
		<logintext label='姓名' style="margin: 15px 0;" placeholder="请输入姓名" rule='^.{6,16}$' 
		@inputchage="content => mode.name=content" 
		/>
		<!-- 触发子元素事件 -->
		<logintext label='账号' placeholder="请输入账号" rule='^.{6,16}$' 
		@inputchage="content => mode.username=content" 
		/>
		<logintext label='密码' placeholder="请输入密码" type="password" rule='^.{6,16}$'
		 @inputchage="content => mode.password=content" 
		 />

		<!-- 按钮               触发子组件的事件 -->
		<loginbtn pasbtn="注册" @regsumid='regsumid'></loginbtn>
	</div>
</template>

<script>
	import logintop from './common/loginTop.vue';
	import logintext from './common/logintext.vue';
	import loginbtn from './common/loginbtn.vue';
	export default {
		data() {
			return { //存储 通过正则表达式的  数据
				mode: {
					name: '',
					username: '',
					password: ''
				}
			}
		},
		components: {
			logintop,
			logintext,
			loginbtn
		},
		methods: {
			async regsumid() { //判读数据，发送ajax
				let rulg = /^.{6,16}$/;
				if (rulg.test(this.mode.name) && rulg.test(this.mode.username) && rulg.test(this.mode.password)) {
					const res = await this.$http.post("/register",this.mode) //异步的代码
					this.$msg.fail(res.data.msg) //显示 后端传递的数据
					// 跳转页面  先将id和token值保存到本地存储
					localStorage.setItem('id',res.data.id);
					localStorage.setItem('token',res.data.objtoken);
					setTimeout(()=>{
						this.$router.push('/userinfo');
					},1000)
					 
				} else {
					this.$msg.fail("格式不正确，重新输入"); //vant组件 Toast中的方法
				}
			}
		}
	}
</script>

<style>
</style>



