<template>
	<div class="navbarq">
		<div class="logo"  @click="aa">
			<img src="../../assets/logo.png" alt="">
		</div>
		<div class="cen">
			<p>
				请输入内容
				<van-icon name="search" class="van-icon"/>
			</p>
		</div>
		<div class="reg">
			<img :src="imgurl" alt="" @click="$router.push('/edit')" v-if="imgurl">
			<img src="../../assets/wo.png" alt="" @click="$router.push('/login')" v-if="!imgurl">
			<p>下载app</p>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				imgurl:'',//接收后端的地址
				a:null
			}
		},
		//绑定之后的函数
		async mounted(){//发送请求，接收头像地址
			if(localStorage.getItem('token')){
				const res=await this.$http.get('/user/'+localStorage.getItem('id'));
				this.imgurl=res.data[0].user_img;
			}
		},
		methods:{
			async aa(){
				this.$router.push('/home');
			}
		}
		
	}
</script>

<style>
	.navbarq{
		height: 12.5vw;
		background-color: #ffffff;
		display: flex;
	}
	.logo{
		display: flex;
		justify-content: center;
		align-items: center;
		width: 110px;
		height:42px;
	}
	.logo img{
		width: 80%;
	}
	.reg{
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.reg img{
		width: 24px;
		height: 24px;
		
	}
	.cen{
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		
	}
	.cen p{
		background-color: #f4f4f4;
		height: 24px;
		width: 90%;
		border-radius: 10px;
		padding-left: 25px;
		color: #aaa;
		position: relative;
	}
	.reg p{
		font-size: 12px;
		line-height: 24px;
		padding: 5px 10px;
		background-color: #fb7299;
		color: #ffffff;
		margin: 0 8px;
		font-size: 13px;
		border-radius: 3px;
	}
	.van-icon{
		color: #aaa;
		position: absolute;
		left: 10px;
		top: 50%;
		transform: translate(0,-50%);
	}
</style>



