<template>
	<!-- 评论组件 -->
	<div class="commtop">
		<div class="commtops" v-for="(i,index) in comlist" :key="index">
			<div class="commcent">
				<div class="comimg">
					<img :src="i.userinfo.user_img" alt="" v-if="i.userinfo && i.userinfo.user_img">
					<img src="@/assets/wo.png" alt="" v-else>
				</div>
				<div class="comimgas">
					<p>
						<span v-if="i.userinfo">{{i.userinfo.name}}</span>
						<span v-else>无名氏</span>
						<span>{{i.comment_date}}</span>
					</p>
					<!-- 评论 -->
					<div class="comimgasfont">
						{{i.comment_content}}
						<span class="huifu" @click="dsiid(i.comment_id)">回复</span>
					</div>
				</div>
			</div>
			<!-- 二级评论 -->
			<div class="res">
				<commentltem :commentchild="i.child" @postpublish="dsiid"></commentltem>
			</div>
			
		</div>
	</div>
</template>

<script>
	import commentltem from './commentltem.vue'
	export default {
		data: function() {
			return {
				comlist: null, //评论数据
			}
		},
		components: {
			commentltem
		},
		methods: {
			async comment() {
				const res = await this.$http.get('/comment/' + this.$route.params.id);
				if(res.data){//这里判断一共多少条数据
					//触发事件，发送给父元素
					this.$emit('seless',res.data.length);//返回的长度参数
				}
				this.comlist = this.chang(res.data); //将获取到的评论放到下面的函数中使用
			},
			chang(arr) { //使用递归 改造评论数据
				function fn(item) {
					let arr1 = [];
					for (var i = 0; i < arr.length; i++) {
						if (arr[i].parent_id == item) {
							arr1.push(arr[i]);
							arr[i].child = fn(arr[i].comment_id);
						}
					}
					return arr1;
				}
				return fn(null);
			},
			//回复事件 参数时上面传来的评论的id
			dsiid(id){
				console.log(id);
				 this.$emit('dsiid',id);
			}

		},
		created() {
			this.comment();
		}
	}
</script>

<style scoped>
	.commtop {
		padding: 0px 10px;
	}

	.commtops {
		border-bottom: 1px solid #e7e7e7;
	}

	.commcent {
		padding: 10px 0;
		display: flex;

	}

	.comimg {
		margin-right: 10px;
	}

	.comimg img {
		width: 25px;
		height: 25px;
		border-radius: 50%;
	}

	.comimgas {
		flex: 1;
	}

	.comimgas p {
		display: flex;
		justify-content: space-between;
		color: #555;
		font-size: 13px;
	}

	.comimgasfont {
		position: relative;
		font-size: 12px;
		margin-top: 5px;
	}
	.res{
			padding: 0 30px;
	}
	.huifu{
		position: absolute;
		right: 0;
		color: red;
	}
</style>



