<template>
	<div class="commone">
		<p class="commone-list">
			<span>评论</span>
			<span>({{delenht}})</span>
		</p>
		<!-- 个人信息· -->
		<div class="commone-user">
			<img :src="myuser.user_img" alt="" v-if="myuser.user_img">
			<img src="@/assets/wo.png" alt="" v-if="!myuser.user_img">
			<input type="text" placeholder="说点什么..." v-model="content" ref="postipt">
			<button @click="focuslist">
				发表
			</button>
		</div>
	</div>
</template>

<script>
	export default {
		data:function(){
			return{
				myuser:{},//自己的信息数据
				content:'',//双向绑定输入框数据
			}
		},
		props:['delenht'],
		created() {
			if(localStorage.getItem('token')){//判断没登录时的操作
				this.myuserinfo();
			}
		},
		methods:{
			//评论 先获取自己的信息
			async myuserinfo(){
				const res=await this.$http.get('/user/'+localStorage.getItem("id"))
				this.myuser=res.data[0];
			},
			// 评论 权限
			focuslist(){
				if(this.myuser && !localStorage.getItem('token') && !localStorage.getItem('token')){
					this.$msg.fail('请先登录');//这里使用提示信息
					return
				}else{//如果登录中，的操作
					
				}
				//触发事件
				this.$emit('postconter',this.content);
				//清空输入框值
				this.content='';
			},
			//聚焦事件
			focusipt(){
				this.$refs.postipt.focus();
			}
		}
	}
</script>

<style scoped>
	.commone{
		padding: 10px 10px;
		
	}
	.commone-list span:nth-child(2){
		color: #aaa;
		margin-left: 15px;
	}
	.commone-user{
		padding: 10px 0;
		display: flex;
	}
	.commone-user img{
		width: 30px;
		height: 30px;
		border-radius: 50%;
	}
	.commone-user input{
		outline: none;
		border: 0;
		background-color: #f4f4f4;
		border-radius: 14.5px;
		margin-left: 15px;
		font-size: 12px;
		padding: 0 20px 0 10px;
	}
	button{
		outline: none;
		border: 0;
		padding: 0 15px;
		border-radius: 15px;
		background-color: #FBC4C4;
		color: #ffffff;
	}
</style>



