<template>
	<div class="userinfo">
		<userinfo></userinfo>
		<img src="../assets/1.png" alt="" class="backimg"><!-- 背景图 -->
		<!-- 个人详情 -->
		<userdeta :userinfo="mode"></userdeta>
		<userAction></userAction>
	</div>
</template>

<script>
	import userinfo from './common/Navbar.vue';
	import userdeta from './usercom/userdeta.vue';
	import userAction from './common/actino.vue'
	export default{
		data:function(){
			return{
				mode:{}
			}
		},
		components:{
			userinfo,
			userdeta,
			userAction
		},
		methods:{
			async userinfo(){
				const res=await this.$http.get('/user/'+localStorage.getItem('id'))
				this.mode=res.data[0]
			}
		},
		created() {//进入页面，触发函数，获取本地存储中的值 发送ajax
			this.userinfo()
		}
	}
</script>

<style>
	.userinfo{
		
	}
	.backimg{
		width: 100%;
		height: 91px;
	}
</style>



