<template>
	<div class="home">
		<!-- 封装的子组件 -->
		<navber></navber>
		<!-- 滚动导航 -->
		<van-tabs v-model="active" swipeable sticky>
			<van-tab v-for="(i,index) in array" :title="i.title" :key='index'>
				<!-- 下拉加载更多 -->
				<van-list v-model="i.loading" :immediate-check='false' :finished="i.finished" finished-text="我是有底线的" @load="onLoad">
					<!-- <van-cell v-for="item in i.list" :key="item" :title="item" /> -->

					<div class="detaparent">
						<!-- 单个文件 -->
						<detail class="detalite" :detailitem='j' v-for="(j,indexs) in i.list" :key="indexs">

						</detail>
					</div>
				</van-list>
			</van-tab>
		</van-tabs>

	</div>
</template>

<script>
	import detail from './Detail.vue'; //引入单个文件
	import navber from './common/Navbar.vue'; //引入头部
	export default {
		data: function() {
			return {
				array: [], //接收ajax导航条的数据
				active: 0
			}
		},
		components: {
			navber,
			detail
		},
		methods: {
			async setlist() { //获取导航条数据
				var res = await this.$http.get('/category');
				this.changCat(res.data);

			},
			changCat(data) { //对拿到的数据进行改造
				const cate = data.map((item, index) => {
					item.list = []; //新增一个数组属性
					item.page = 0; //当前页码
					item.pagesize = 10; //每页显示的数据
					item.loading = false; //下拉框加载属性
					item.finished = false; //下拉框加载属性

					return item;
				})
				//赋值给array数组
				this.array = cate;
				//在调用ajax
				this.selectAticle();
			},
			async selectAticle() { //获取详情列表
				//接收一下选中的下标
				const lista = this.catgoryitem();
				const res = await this.$http.get('/detail/' + lista._id, {
					params: {
						page: lista.page,
						pagesize: lista.pagesize
					}
				});
				lista.list.push(...res.data); //将获取的的详情数据放到新建的list中 es6语法
				lista.loading = false;//控制加载状态
				if(res.data.length<lista.pagesize){//判断是否加载完毕
					lista.finished=true;
				}
			},
			catgoryitem() { //获取当前选中的下标
				const catgoryitem = this.array[this.active];
				return catgoryitem
			},
			onLoad() { //下拉框事件
				const res = this.catgoryitem() //先获取当前下拉的式那个分类
				setTimeout(() => { //延迟一秒发生
					//修改当前页码
					res.page += 1;
					//发生ajax,显示数据
					this.selectAticle();
				}, 1000)

			}

		},
		//监听事件
		watch: {
			active() { //数据发生改变时，触发事件
				this.selectAticle();
			}
		},
		created() {
			this.setlist();

		}
	}
</script>

<style scoped>
	.detaparent {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}

	.detalite {
		width: 45%;
		margin: 5px 0;
	}

	.home {
		background-color: #ffffff;
	}
</style>



