<template>
	<div id="app">
		<keep-alive><!-- 这个标签可以 保持页面状态，不会被销毁 -->
			<router-view v-if="$route.meta.keepalive"/><!-- 如果存在这个属性，就显示 -->
		</keep-alive>
		<router-view v-if="!$route.meta.keepalive"></router-view>
	</div>
</template>

<script>
	export default {
		name: 'App'
	}
</script>

<style>
	body{
		margin: 0;
		padding: 0;
		box-sizing: border-box;
		background-color: #f4f4f4;
	}
	p{
		margin: 0;
		padding: 0;
	}
</style>



