<template>
	<div v-if="mode" class="aaa">
		<div style="margin-bottom: 15px;">
			<Navbar></Navbar>
		</div>
		<div class="ss">
			<!-- 上传头像 -->
			<van-uploader :after-read="afterRead" preview-size="100vw" class="ssimg" />
			<editbanner left='头像'>
				<img :src="mode.user_img" alt="" slot='right' v-if="mode.user_img">
				<img src="@/assets/2.jpg" alt="" slot='right' v-else>
			</editbanner>
		</div>
		<editbanner left='昵称' @asd='show=true'><!-- 接收子元素传递的事件 -->
			<!-- 插槽占位符 -->
			<a href="javascript:;" slot='right'>{{mode.name}}</a>
		</editbanner>
		<editbanner left='UID'>
			<a href="javascript:;" slot='right'>{{mode.username}}</a>
		</editbanner>
		<editbanner left='性别' @asd='ger=true'>
			<span slot='right'>
				{{mode.gender=='1'?'男':'女'}}
			</span>
		</editbanner>
		<editbanner left='个性签名' @asd='textshow=true'>
			<span slot='right'>
				{{mode.user_desc}}
			</span>
		</editbanner>
		
		<!-- 返回按钮 -->
		<div class="asdiv" @click="$router.back()"><!-- 返回上一级 -->
			返回个人中心
		</div>
		<!-- 弹出框  昵称-->
		<van-dialog v-model="show" title="昵称" show-cancel-button @confirm='confirm' @cancel='cancel'>
		 <van-field v-model="text" autofocus />
		</van-dialog>

		<!-- 个性签名 -->
		<van-dialog v-model="textshow" title="个性签名" show-cancel-button @confirm='confirmq' @cancel='cancel'>
		 <van-field v-model="text" autofocus type='textarea'/>
		</van-dialog>
		
		<!-- 性别框 -->
		<van-action-sheet v-model="ger" cancel-text="取消" :actions="actions" @select="onSelect" />
	</div>
</template>

<script>
	import Navbar from './common/Navbar.vue'
	import editbanner from './common/editbanner.vue'
	export default {
		data: function() {
			return {
				show:false,//昵称框的显示
				textshow:false,//个签框的显示
				ger:false,//框的显示
				text:'',
				mode: '', //存放ajax请求的数据
				actions: [//性别框数据
				        { name: '男',val:1},
						 { name: '女',val:0},
				      ],
			}
		},
		components: {
			Navbar,
			editbanner
		},
		methods: {
			async setedit() { //发送ajax
				const res = await this.$http.get('/user/' + localStorage.getItem('id'))
				this.mode = res.data[0];
			},
			//上传头像的方法
			async afterRead(file) {
				// 此时可以自行将文件上传至服务器
				const fromdata=new FormData()
				fromdata.append('file',file.file);
				const res=await this.$http.post('/upload',fromdata);
				//将图片地址修改为自己传入的
				this.mode.user_img=res.data.url;
				this.userUpda();//调用函数
			},
			//修改后端的图片地址
			async userUpda(){
				const res=await this.$http.post('/update/'+localStorage.getItem('id'),this.mode)
				if(res.data.code==200){//给出提示框
					this.$msg.fail('修改成功');
				}
			},
			confirm(){//点击确认触发的事件
				this.mode.name=this.text;//获取输入后的值
				this.userUpda();//发送ajax
				this.text='';//清空text中的值，好让后面的使用
			},
			cancel(){//取消时触发事件
				this.text='';
			},
			//个性签名触发事件
			confirmq(){
				this.mode.user_desc=this.text;//修改为输入的值，然后发送ajax
				this.userUpda();
				this.text='';//清空text中的值，好让后面的使用
			},
			//性别框 触发的事件
			onSelect(data){//这里的参数data 选中的信息
				this.mode.gender=data.val;//将后端的数据修改为选中的值，
				this.userUpda();//发送ajax
				this.ger=false;//将性别框隐藏
			}
		},
		created() { //页面加载执行方法
			this.setedit();
		}
	}
</script>

<style scoped>
	.aaa a {
		color: #333;
	}

	.aaa img {
		width: 46px;
		height: 46px;
		border-radius: 50%;
	}

	.ss {
		position: relative;
		overflow: hidden;
	}

	.ssimg {
		opacity: 0;
		position: absolute;
	}
	.asdiv{
		background-color: #ffffff;
		color: #999;
		display: flex;
		margin-top: 20px;
		padding: 15px 0;
		justify-content: center;
		align-items: center;
		font-size:4vw;
	}
</style>



