<template>
	<div class="ltemtop">
		<div class="commcent" v-for="(i,index) in commentchild" :key="index">
			<div class="comimg">
				<img :src="i.userinfo.user_img" alt="" v-if="i.userinfo && i.userinfo.user_img">
				<img src="@/assets/wo.png" alt="" v-else>
				<p>
					<span v-if="i.userinfo.name">{{i.userinfo.name}}</span>
					<span v-else>无名氏</span>
					<span>{{i.comment_date}}</span>
				</p>
			</div>
			<div class="comimgas">
				<!-- 评论 -->
				<div class="comimgasfont">
					<!-- 判断是不是二级评论和在下级评论 -->
					<div v-if="!item">
						{{i.comment_content}}
						<span class="huifu" @click="asdpost(i.comment_id)">回复</span>
					</div>
					<div v-else>
						回复：<span style="color: #0000ED;">{{i.parent_user_info.name}}</span> 
						{{i.comment_content}}
						<span class="huifu" @click="asdpost(i.comment_id)">回复</span>
					</div>
				</div>
			</div>
			<!-- 递归组件 -->
			<commchild-i :commentchild='i.child' :item='true' @asdlol='polol'></commchild-i>
		</div>
	</div>
</template>

<script>
	export default{
		name:'commchild-i',
		props:['commentchild','item'],
		methods:{
			//回复二级或三级评论
			asdpost(id){
				this.$emit('asdlol',id);
				this.$emit('postpublish',id);
			},
			polol(id){
				//递归组件的函数
				this.asdpost(id);
				this.$emit('postpublish',id);
			}
		}
	}
</script>

<style scoped>
	.ltemtop{
		padding-bottom: 10px;
	}
	.commtops{
		border-bottom: 1px solid #e7e7e7;
	}
	.commcent{
		padding: 10px 0;
		display: flex;
		flex-direction: column;
	}
	.comimg{
		margin-right: 10px;
		display: flex;
		
	}
	.comimg img{
		margin: 0 5px 10px;
		width: 25px;
		height: 25px;
		border-radius: 50%;
	}
	.comimgas{
		flex: 1;
	}
	.comimg p{
		flex: 1;
		display: flex;
		justify-content: space-between;
		color: #555;
		font-size: 13px;
	}
	.comimgas p{
		display: flex;
		justify-content: space-between;
		color: #555;
		font-size: 13px;
	}
	.comimgasfont{
		font-size: 12px;
		margin-top: 5px;
		position: relative;
	}
	.huifu{
		position: absolute;
		right: 10px;
		color: red;
	}
</style>



