<template>
	<div @click="$router.push(`/article/${detailitem.id}`)">
		<div class="dsada">
			<!-- 单个视频文件 -->
			<div class="imgparms">
				<img :src="detailitem.img" alt="" style="width: 100%;">
				<div class="alp">
					<div class="icon-play2">
						<span class="vode">
							1亿...
						</span>
					</div>
					<div class="icon-file-text">
						<span class="comment ">
							{{!detailitem.commentlen?66:detailitem.commentlen}}
						</span>
					</div>
				</div>
				<p>{{detailitem.name}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['detailitem']
	}
</script>

<style scoped>
	.dsada p {
		font-size: 12px;
	}

	.imgparms {
		position: relative;
		left: 0px;
		bottom: 0;
	}

	/* 	.vode {
		position: absolute;
		left: 0px;
		bottom: 0;
	}

	.comment {
		position: absolute;
		right: 5px;
		bottom: 0;
	} */

	.alp {
		color: #ffffff;
		background: linear-gradient(0deg, rgba(0, 0, 0, .85), transparent);
		bottom: 15px;
		margin-bottom: 5px;
		position: absolute;
		padding: 0 3px;
		justify-content: space-around;
		display: flex;
		left: 0;
		right: 0;
	}
</style>



