<template>
	<div>
		  <van-field 
		  v-model="content" 
		  :label="label" 
		  :type='type' 
		  :placeholder="placeholder" 
		  :rule="rule"
		 
		  />
	</div>
</template>

<script>
	export default{
		props:['label','type','placeholder','rule'],
		data:function(){
			return{
				content:""
			}
		},
		methods:{
			handleru(){
				const rul=new RegExp(this.rule);//将字符串转化为正则表达式
				if(rul.test(this.content)){//使用正则表达式判断输入的值,校验成功，触发事件给父元素
					this.$emit('inputchage',this.content);
				}
			}
		},
		watch:{//监听
			content(){//content的值改变时
				this.handleru();
			}
		}
	}
</script>

<style>
</style>



