<template>
	<div v-if="model">
		<navber></navber>
		<div class="app">
			<div class="video">
				<!-- 视频 -->
				<video :src="model.content" controls="controls"></video>
			</div>
			<!-- 视频标题 -->
			<div class="videoinfo">
				<div>
					<span class="span">{{model.category.title}}</span>
					<span>{{model.name}}</span>

				</div>
				<div class="infos">
					<span>{{model.userinfo.name}}</span>
					<span>145.25万播放</span>
					<span>6257弹幕</span>
					<span>{{model.date}}</span>
				</div>
				<div class="plp">
					<div @click="shoucang">
						<span class="icon-star-full" :class="{spans:collol}"></span>
						<span  :class="{spans:collol}">收藏</span>
					</div>
					<div @click="substr">
						<span class="icon-bubble" :class="{spans:substrs}"></span>
						<span :class="{spans:substrs}">关注</span>
					</div>
					<div>
						<span class=" icon-redo2"></span>
						<span>分享</span>
					</div>
				</div>
				<!-- 推荐视频 -->
				<div class="detaparent">
					<!-- 视频组件 动态数据传递给子组件-->
					<detail v-for="(i,index) in commendlist" :key="index" :detailitem='i' class="detalite" />
				</div>
				<!-- 评论 组件 将评论长度在发送给组件 -->
				<commone :delenht='lenht' @postconter="postconter" ref='commentipt'></commone>
				<!-- 真正评论  接收子组件的事件  -->
				<comment @seless='selelenght' ref='commpy' @dsiid='postdsiid'></comment>
			</div>
		</div>
	</div>
</template>

<script>
	import comment from './article/comment.vue'
	import commone from "./article/commone.vue"
	import detail from './Detail.vue'
	import navber from './common/Navbar.vue'

	export default {
		data: function() {
			return {
				model: null, //存放ajax数据
				commendlist: null, //存放推荐文章的数据
				lenht: 0, //评论长度
				postcom: { //发送评论的对象数据
					comment_content: '', //发表的评论
					comment_date: '', //发表的时间
					parent_id: null, //一级评论的id
					article_id: null, //评论的文章id
				},
				collol: null, //收藏控制样式的
				substrs:null,//关注用户的
			}
		},
		components: {
			navber,
			detail,
			commone,
			comment
		},
		methods: {
			async articlei() { //发生请求详情信息的ajax
				const res = await this.$http.get('/article/' + this.$route.params.id);
				this.model = res.data[0];
				//存在数据在判断是否关注
				if(this.model){
					this.kikis();
				}
			},
			//获取推荐文章
			async commend() {
				const res = await this.$http.get('/commend');
				this.commendlist = res.data;
				
			},
			//评论长度
			selelenght(lenght) {
				this.lenht = lenght; //将获得的长度 赋值给变量
			},
			//发送一级评论
			async postconter(res) {
				//发表时间
				const date = new Date();
				let m = date.getMonth() + 1; //月
				let d = date.getDate(); //日
				if (m < 10) {
					m = "0" + m
				}
				if (d < 10) {
					d = '0' + d
				}
				let str = `${m}-${d}`
				console.log(str);
				//评论
				this.postcom.comment_content = res;
				this.postcom.comment_date = str;
				this.postcom.article_id = this.$route.params.id; //赋值当前评论的id
				//发送ajax传递参数，发表评论
				const ress = await this.$http.post('/comment_post/' + localStorage.getItem('id'), this.postcom);
				//重新渲染
				this.$refs.commpy.comment()
				//将一级评论的id清空
				this.postcom.parent_id = null;
				//评论提示消息
				if (ress.status == 200) {
					this.$msg.fail('评论完成');
				}
			},
			//回复评论
			postdsiid(id) {
				this.postcom.parent_id = id; //将点击时的id 赋值给 一级评论的id
				this.$refs.commentipt.focusipt(); //调用子组件中的方法
			},
			//收藏文章
			async shoucang() {
				//判断是否用户登录，再发送收藏
				if (localStorage.getItem('token') && localStorage.getItem('id')) {
					const res = await this.$http.post('/collection/' + localStorage.getItem('id'), {
						article_id: this.model.userid
					})
					if (res.data.msg == '收藏成功') {
						this.collol = true;
					} else {
						this.collol = false;
					}
					this.$msg.fail(res.data.msg)
				}
			},
			//进入页面获取是否收藏
			async kiki() {
				if (localStorage.getItem('token') && localStorage.getItem('id')) {
					const res = await this.$http.get('/collection/' + localStorage.getItem('id'), {
						params:{
							article_id: this.$route.params.id
						}
					})
					console.log(res);
					this.collol=res.data.success;
				}
			},
			//关注发帖用户
			async substr() {
				//判断是否用户登录，再发送收藏
				if (localStorage.getItem('token') && localStorage.getItem('id')) {
					const res = await this.$http.post('/sub_scription/' + localStorage.getItem('id'), {
						sub_id: this.model.userid
					})
					if (res.data.msg == '关注成功') {
						this.substrs = true;
					} else {
						this.substrs = false;
					}
					this.$msg.fail(res.data.msg)
				}
			},
			//进入页面获取是否关注
			async kikis() {
				if (localStorage.getItem('token') && localStorage.getItem('id')) {
					const res = await this.$http.get('/sub_scription/' + localStorage.getItem('id'), {
						params: {
							sub_id: this.model.userid
						}
					})
					this.substrs=res.data.success;
				}
			},
		},
		created() {
			this.articlei();
			this.commend();
			this.kiki();
		},
		//监听 ，当前路由是否有变化
		watch: {
			$route() { //如果变化，就在发生ajax请求
				this.articlei();
				this.commend();
				this.kiki();
			}
		}
	}
</script>

<style scoped>
	.video {
		width: 100%;
	}

	.video video {
		width: 100%;
	}

	.videoinfo {
		background-color: #ffffff;
		padding: 15px;
	}

	.span {
		padding: 0 10px;
		color: #fb7299;
		background-color: #f4f4f4;
		border-radius: 10px;
	}

	.infos {
		padding: 16px 10px;
	}

	.infos span {
		color: #aaa;
		font-size: 12px;
		padding: 10px;
	}

	.infos span:nth-child(1) {
		color: #000000;
		font-size: 16px;
		padding-left: 0;
		padding-right: 10px;
	}

	.plp {
		padding: 10px;
		display: flex;
	}

	.plp div {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 25px;
		color: #757575;
	}

	.plp div span:nth-child(1) {
		font-size: 21px;
		padding-right: 3px;
	}

	.plp div span:nth-child(2) {
		font-size: 12px;
	}

	.detaparent {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
	}

	.detalite {
		width: 45%;
		margin: 5px 0;
	}

	/* 收藏颜色 */
	.spans {
		color: palevioletred;
	}
</style>



