<template>
	<div class="top">
		<div></div>
		<div>{{ midee }}</div>
		<div>
			<slot name="right"></slot>
		</div>
	</div>
</template>

<script>
	export default {
		props: ["midee"],
	};
</script>

<style>
	* {}

	.top {
		height: 45px;
		background-color: #ffffff;
		display: flex;
	}

	.top div {
		background-color: #ffffff;
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 16px;
	}
</style>



